import { File, NewFile, OldFile, S3Url } from '../types';
import _isEmpty from 'lodash/isEmpty';
import { v4 as uuidv4 } from 'uuid';
import { FILE_ADD_ACTION } from '../offers/contants';

export const generateFiles = (files: Array<File>) => {
  return files.map((file: File) => {
    const { rawFile } = file;
    if (_isEmpty(rawFile)) return { serverContent: file };
    const { name, path, size, type } = rawFile;
    const id = uuidv4();
    return {
      id,
      serverContent: {
        action: FILE_ADD_ACTION,
        name,
        file: {
          path,
        },
        id,
        size,
        type,
      },
      rawFile,
    };
  });
};

export const mappingS3UrlWithFile = (
  urls: Array<S3Url>,
  files: Array<OldFile | NewFile>
) => {
  return files.map((file: any) => {
    const url = urls.find((urlItem: S3Url) => urlItem.id === file.id);
    if (_isEmpty(url)) return false;
    return {
      ...file,
      ...url,
    };
  });
};
