import { OFFER_STEPS } from 'configs/constants';
import _get from 'lodash/get';
import { Offer } from 'types';

export const isWaitingAdvisorApproval = (offer: Offer) =>
  _get(offer, 'CurrentStep') === OFFER_STEPS.awaitingNileAdvisorApproval;

export const isPABeingSigned = (offer: Offer) =>
  _get(offer, 'CurrentStep') === OFFER_STEPS.paBeingSigned;

export const isPABeingCreated = (offer: Offer) =>
  _get(offer, 'CurrentStep') === OFFER_STEPS.paBeingCreated;
