import React, { FC } from 'react';
import {
  Edit,
  EditProps,
  SimpleForm,
  TextField,
  Toolbar,
  SaveButton,
  FileInput,
  FileField,
  useNotify,
  SaveButtonProps,
  useDataProvider,
  FormDataConsumer,
} from 'react-admin';
import { useCallback } from 'react';
import _forEach from 'lodash/forEach';
import { OfferActions } from './OfferActions';
import {
  UPDATE_FIELD_NAME,
  OFFER_RESOURCE_NAME,
  FILE_UPLOAD_LIMIT,
} from './contants';
import { File, NewFile } from 'types';
import { generateFiles, mappingS3UrlWithFile } from 'helpers/S3';
import _get from 'lodash/get';
import { API_ENDPOINT, OFFER_STEPS } from 'configs/constants';

const SaveOffer = (props: SaveButtonProps) => {
  const notify = useNotify();
  const dataProvider = useDataProvider();

  const uploadFilesToS3 = async (files: Array<NewFile>) => {
    _forEach(files, (file: File) => {
      if (file) {
        const { url, rawFile } = file;
        if (url && rawFile) {
          dataProvider.uploadToS3(url, { data: rawFile });
        }
      }
    });
  };

  const handleSave = useCallback(
    async (values, redirect) => {
      try {
        const { SignedPurchaseAgreement, id } = values;
        const planFields = generateFiles(SignedPurchaseAgreement);
        const uploadUrl = `${API_ENDPOINT}/${OFFER_RESOURCE_NAME}/${id}/files`;
        const files = planFields.map((file: any) => {
          const { serverContent } = file;
          return serverContent;
        });
        const { data } = await dataProvider.upload(uploadUrl, {
          data: { file_type: UPDATE_FIELD_NAME, files },
        });
        const { urls } = data;
        const s3Files = mappingS3UrlWithFile(urls, planFields);
        await uploadFilesToS3(s3Files);
        notify('File uploaded');
        redirect(OFFER_RESOURCE_NAME);
      } catch (error) {
        notify(`Cannot upload files`, 'warning');
      }
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [notify, dataProvider]
  );
  return <SaveButton {...props} onSave={handleSave} />;
};

const isShowStep200 = (currentStep: number) =>
  currentStep === OFFER_STEPS.paBeingSigned;

const OfferEditToolbar: FC = props => {
  const currentStep = _get(props, ['record', 'CurrentStep']);
  return isShowStep200(currentStep) ? (
    <Toolbar {...props}>
      <SaveOffer submitOnEnter={true} />
    </Toolbar>
  ) : (
    <></>
  );
};

const OfferEdit: FC<EditProps> = props => {
  return (
    <Edit actions={<OfferActions />} {...props}>
      <SimpleForm toolbar={<OfferEditToolbar />}>
        <TextField source="id" label="ID" />
        <TextField source="PropertyId" label="Property ID" />
        <TextField source="PropertyAddress" label="Property Address" />
        <TextField source="Step.messages.advisor" label="Current Status" />
        <TextField source="SellerName" label="Seller Name" />
        <TextField source="BuyerName" label="Buyer Name" />
        <TextField source="BuyerAgentName" label="Buyer Agent Name" />
        <FormDataConsumer>
          {({ formData, ...rest }) => {
            const { CurrentStep } = formData;
            return (
              isShowStep200(CurrentStep) && (
                <FileInput
                  source={UPDATE_FIELD_NAME}
                  label="Signed Purchase Agreement"
                  accept="application/pdf"
                  multiple={true}
                  maxSize={FILE_UPLOAD_LIMIT}
                >
                  <FileField source="file.path" title="name" />
                </FileInput>
              )
            );
          }}
        </FormDataConsumer>
      </SimpleForm>
    </Edit>
  );
};

export default OfferEdit;
