import React, { FC } from 'react';
import { TopToolbar } from 'react-admin';
import { ButtonAction } from './ButtonAction';
import _get from 'lodash/get';
import { OFFER_STEPS } from 'configs/constants';
import {
  isPABeingCreated,
  isPABeingSigned,
  isWaitingAdvisorApproval,
} from 'helpers/Offer';

export const OfferActions: FC = props => {
  // use lodash _get, avoid props validated
  const resource = _get(props, 'resource');
  const offer = _get(props, 'data');
  const offerId = _get(offer, 'id');

  // TODO: check input upload

  return (
    <TopToolbar>
      {isWaitingAdvisorApproval(offer) && (
        <ButtonAction
          text="Approve"
          resource={resource}
          payload={{
            id: `${offerId}/steps`,
            data: { new_step: OFFER_STEPS.awaitingSellerAction },
          }}
        />
      )}
      {isPABeingSigned(offer) && (
        <ButtonAction
          text="PA Signed"
          resource={resource}
          payload={{
            id: `${offerId}/steps`,
            data: { new_step: OFFER_STEPS.confirmTransfer },
          }}
        />
      )}
      {isPABeingCreated(offer) && (
        <ButtonAction
          text="PA Created"
          resource={resource}
          payload={{
            id: `${offerId}/steps`,
            data: { new_step: OFFER_STEPS.paBeingSigned },
          }}
        />
      )}
    </TopToolbar>
  );
};
