import React from 'react';
import { FC } from 'react';
import {
  ListProps,
  List,
  Pagination,
  PaginationProps,
  Datagrid,
  TextField,
  EditButton,
} from 'react-admin';
import { ITEM_PER_PAGE } from 'configs/list';

const OfferPagination: FC<PaginationProps> = props => <Pagination {...props} />;

const OfferList: FC<ListProps> = props => (
  <List
    {...props}
    perPage={ITEM_PER_PAGE}
    pagination={<OfferPagination />}
    component="div"
    actions={false}
  >
    <Datagrid>
      <TextField source="id" label="ID" />
      <TextField source="PropertyId" label="Property ID" />
      <TextField source="PropertyAddress" label="Property Address" />
      <TextField source="Step.messages.advisor" label="Current Status" />
      <TextField source="SellerName" label="Seller Name" />
      <TextField source="BuyerName" label="Buyer Name" />
      <TextField source="BuyerAgentName" label="Buyer Agent Name" />
      <EditButton />
    </Datagrid>
  </List>
);

export default OfferList;
