import React, { FC } from 'react';
import { useMutation, useNotify, useRedirect } from 'react-admin';
import { Button } from '@material-ui/core';
import _get from 'lodash/get';
import { OFFER_PATH } from './router';

interface Props {
  payload: object;
  resource: string;
  text: string;
}

export const ButtonAction: FC<Props> = props => {
  const { resource, payload, text } = props;

  const notify = useNotify();
  const redirect = useRedirect();

  const [onClickHandler, { loading }] = useMutation(
    {
      type: 'update',
      resource,
      payload,
    },
    {
      onSuccess: () => {
        redirect('list', OFFER_PATH);
        notify('Update success', 'info');
      },
      onFailure: error =>
        notify(`Update error: ${_get(error, ['message'])}`, 'warning'),
    }
  );

  return (
    <Button color="primary" onClick={onClickHandler} disabled={loading}>
      {text}
    </Button>
  );
};
